.section-home {
  flex: 1;
  display: flex;
  flex-direction: column;

  /* margin: 0.5rem auto; */
  /* margin: 1.5rem auto; */
  margin: 1rem auto;
  margin-bottom: 2rem;
  padding: 0.25rem;
  /* min-height: 300px; */
  position: relative;
  overflow-y: auto;
  /* border: 1px solid red; */
  /* overflow: hidden; */
  border: 1px solid yellow;
}
.prev-index-btn,
.next-index-btn {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 0.5rem;
  background-color: transparent;
  border-color: transparent;
  opacity: 0.5;
  top: calc(70% / 2);
  transform: translateY(-50%);
  color: #000;
  /* color: #fff; */
  cursor: pointer;
}
.prev-index-btn {
  left: 0;
}
.next-index-btn {
  right: 0;
}
.prev-index-btn:hover,
.next-index-btn:hover {
  opacity: 1;
}
.preview-wrapper {
  flex: 0 0 100px;

  display: flex;
  /* justify-content: center; */
  align-items: center;
  /* width: 100%; */
  /* height: 100%; */
  /* overflow: hidden; */
  /* overflow-x: auto; */
  /* overflow: auto; */
  /* border: 3px solid yellow; */
  border: 1px solid yellow;
}
.preview-wrapper button {
  /* border: 1px solid red; */
  height: 100%;
  background-color: #f2f2f2;
  border-color: transparent;
}
@media screen and (max-width: 479px) {
  .section-home {
    margin: 0.5rem auto;
    min-height: 500px;
  }
  .prev-index-btn,
  .next-index-btn {
    top: calc(75% / 2);
    font-size: 3rem;
  }
  .prev-index-btn {
    margin-left: 0.25rem;
  }
  .next-index-btn {
    margin-right: 0.25rem;
  }
  .preview-wrapper {
    flex: 0 0 70px;
  }
}

