.section-center {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  grid-template-columns: 1fr 1fr 1fr;
  /* grid-template-columns: repeat(20, 1fr); */
  grid-template-rows: auto 1fr;

  gap: 0.5rem;
}
.home-article {
  /* border: 1px solid red; */
}
.home-article {
  display: none;
  height: 50vh;
}
.lastSlide.home-article {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  display: block;
  /* grid-area: last; */
}
.activeSlide.home-article {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  /* grid-area: active; */
  display: block;
}
.nextSlide.home-article {
  grid-column: 3 / 4;
  grid-row: 1 / 2;
  /* grid-area: next; */
  display: block;
}
.home-title-center {
  display: block;
  /* grid-column: 1 / 21; */
  /* grid-row: 2 / 3; */
  /* grid-area: title; */
  grid-column: 1 / 4;
  grid-row: 2 / 3;
  display: flex;
  justify-content: center;
  /* align-items: center; */
  border: 1px solid yellow;
}

.img-center {
  position: relative;
  width: 100%;
  height: 100%;
}
.home-img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: fill;
}
.hiddenSlide {
  display: none;
}

@media screen and (max-width: 479px) {
  .section-center {
    grid-template-columns: 0.7fr 2fr 0.7fr;
  }
  .home-article {
    height: 55vh;
    max-height: 450px;
  }
  .home-title-center {
    font-size: 1.4rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .section-center {
    grid-template-columns: 0.7fr 2fr 0.7fr;
  }
  .home-title-center {
    font-size: 1.5rem;
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .section-center {
    grid-template-columns: 0.9fr 2fr 0.9fr;
  }
  .home-title-center {
    font-size: 1.6rem;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1279px) {
  .section-center {
    grid-template-columns: 0.9fr 2fr 0.9fr;
  }
  .home-title-center {
    font-size: 1.7rem;
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  .section-center {
    grid-template-columns: 1fr 2fr 1fr;
  }
  .home-title-center {
    font-size: 1.8rem;
  }
}
@media screen and (min-width: 1440px) {
  .section-center {
    grid-template-columns: 1fr 2fr 1fr;
  }
  .home-title-center {
    font-size: 1.9rem;
  }
}
