.movie {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  /* border: 0.5px solid rgba(242, 242, 242, 0.3); */
  border-radius: 0.25rem;
  padding: 0.25rem;
  border: 1px solid rgba(244, 244, 244, 0.4);
  /* overflow: hidden; */
}
.movie:hover {
  /* border: 0.5px solid rgba(242, 242, 242, 0.7); */
  border: 1px solid rgba(244, 244, 244, 0.8);
}
.movie-center {
  flex: 1;
  flex-shrink: 0;
  width: 100%;
  cursor: pointer;
  position: relative;
  /* border: 1px solid red; */
}
.movie-img {
  position: absolute;
  width: 100%;
  height: 100%;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  object-fit: fill;
  /* border: 1px solid red; */
}
.movie-info {
  flex: 0 0 25%;
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  overflow: hidden;
  /* border: 1px solid red; */
}

.movie-info h3 {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1.1rem;
  padding: 0.25rem;
  color: #f4f4f4;
  overflow-y: auto;
  flex-wrap: wrap;
  word-break: break-word;
  /* display: block; */
}

.btn-details {
  flex: 0 0 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: #f4f4f4;
  cursor: pointer;
  /* border: 1px solid red; */
  /* border: 0.5px solid rgba(255, 255, 255, 0.4); */
}

@media screen and (max-width: 479px) {
  .movie-info {
    flex: 0 0 25%;
    /**/
    flex: 0 0 30%;
  }
  .movie-info h3 {
    font-size: 1rem;
  }
  .btn-details {
    font-size: 0.9rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .movie-info {
    flex: 0 0 25%;
    /**/
    flex: 0 0 30%;
  }
  .movie-info h3 {
    font-size: 1.05rem;
  }
  .btn-details {
    font-size: 0.95rem;
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .movie-info {
    flex: 0 0 30%;
    /**/
    flex: 0 0 35%;
  }
  .movie-info h3 {
    font-size: 1.1rem;
  }
  .btn-details {
    font-size: 1rem;
  }
}
@media screen and (min-width: 1024px) and (max-width: 1279px) {
  .movie-info {
    flex: 0 0 30%;
    /**/
    flex: 0 0 35%;
  }
  .movie-info h3 {
    font-size: 1.2rem;
  }
  .btn-details {
    font-size: 1.05rem;
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  .movie-info {
    flex: 0 0 35%;
    /**/
    flex: 0 0 40%;
  }
  .movie-info h3 {
    font-size: 1.3rem;
  }
  .btn-details {
    font-size: 1.1rem;
  }
}
@media screen and (min-width: 1440px) {
  .movie-info {
    flex: 0 0 35%;
    /**/
    flex: 0 0 40%;
  }
  .movie-info h3 {
    font-size: 1.4rem;
  }
  .btn-details {
    font-size: 1.15rem;
  }
}
